.navbar {
  background-color: white;
  position: fixed;
  width: 100%;
  left: 0;
  height: 90px;
  font-size: 30px;
  transition: all 1s ease;
  z-index: 10;
  top: 0;
}
/* nav bar when scroll change */
.navbar.colorChange {
  background-color: white;
  box-shadow: 1px 1px 17px rgb(212, 208, 208);
  height: 100px;
  font-size: 20px;
  color: black;
  position: fixed;
  margin-bottom: 30px;
  top: 0;
}
