  margin-top: 50px;
  padding: 10px;
  margin-bottom: 90px;
  color: rgb(36, 34, 34);
  font-size: 32px;
text-align: center;
border: 1px solid #eee;
}
/* card */
.cart .container .row .cards .card {
  height: 180px;
  display: flex;
  flex-flow: row;
  width: 60%;
  margin: auto;
  margin-top: 20px;
  padding: 10px;
  text-transform: capitalize;
  box-shadow: 1px 1px 15px rgb(241, 239, 239);
  border: none;
  border-bottom: 2px solid gray;
}

.cart .container .row .cards .card:hover {
  box-shadow: 1px 1px 16px rgb(230, 227, 227);
}
/* card-contain */
.cart .container .row .cards .card img {
  height: 160px;
  width: 250px;
}
/* right */
.cart .container .row .cards .card .textsss {
  margin-left: 50px;
  margin-top: 10px;
}

.cart .container .row .cards .card .textsss h4 {
  color: #0000ff;
}

.cart .container .row .cards .card .textsss h6 {
  color: black;
  font-size: 20px;
  font-weight: normal;
}
/* trash button */
.cart .container .row .cards .card .textsss .cartbtn {
  position: absolute;
  right: 0;
  top: 0;
  background-color: transparent;
  font-size: 24px;
  color: #db3d50;
  border: none;
}

.cart .total {
  display: flex;
  justify-content: space-around;
  margin-top: 70px;
}

.cart .total .btn {
  background-color: #db3d50;
  color: white;
  width: 140px;
  padding: 10px;
  /* border-radius: 10px; */
}
.cart .total .btn:hover {
  background: #a53845;
}
